let moveTimers = [];
let bumpsTimers = [];
let bumpsTimer;

export function fakeCarMove(callback) {
  let starttime = data.path[1].timestamp;

  for(var i = 1; i < data.path.length; i++) {
    ((index) => {
      let location = {
        coords: {
          latitude: data.path[index].lat,
          longitude: data.path[index].lng
        },
        timestamp: data.path[index].timestamp
      }
      moveTimers[index] = setTimeout(() => callback(location), (data.path[index].timestamp - starttime)/3);
    })(i);
  }
}

export function fakeBumps(callback) {
  let starttime = data.path[1].timestamp;

  for(var i = 1; i < data.bumps.length; i++) {
    ((index) => {
      bumpsTimers[index] = setTimeout(() => callback(data.bumps[index]), (data.bumps[index].timestamp - starttime)/3);
    })(i);
  }
}

export function fakeAcceleration(callback) {
  bumpsTimer = setInterval(() => callback({
        x: Math.random() * 6,
        y: Math.random() * 6,
        z: Math.random() * 6,
      }), Math.random() * 1200 + 300);
}

export function stopCarMoveSimulation() {
  moveTimers.forEach(t => clearTimeout(t));
}

export function stopBumpsSimulation() {
  bumpsTimers.forEach(t => clearTimeout(t));
}

export function stopAccelerationSimulation() {
  clearTimeout(bumpsTimer);
}